.forecasts {
	position: relative;
	&__content {
		display: flex;
		flex-direction: column;
		padding-top: 84px;
		align-items: flex-end; }
	&__text {
		margin: 0 90px 210px 0;
		font-weight: 600;
		color: #fff;
		text-align: center;
		// float: right
		h3 {
			font-size: 72px;
			margin-bottom: 10px;
			font-weight: 600; } }
	&__subtitle {
		font-size: 30px;
		margin-bottom: 25px;
		line-height: 1.5; }

	.box_turquoise {
		top: 248px;
		left: 132px; }
	.box_blurred {
		top: 390px;
		right: 190px; }
	&_bg {
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 1166px;
		background: url(../img/forecast/forecasts_bg.png) no-repeat;
		background-size: cover;
		z-index: -5; } }

.forecasts__video {
	position: relative;
	width: 100%;
	z-index: -1;

	.robot {
		position: absolute;
		top: -759px;
		left: -80px;
		// z-index: -1
		img {
			display: block;
			width: 100%; }
		// &__eyeLight
		// 	position: absolute
		// 	top: -185px
 } }		// 	left: 355px
.video {
	position: relative;
	padding: 48px;
	width: 100%;
	height: 720px;
	overflow: hidden;
	background-color: #fff;
	border-radius: 15px;
	// z-index: -2
	video {
		width: 100%;
		height: 100%;
		border-radius: 15px; } }
.video__play {
	&_wrapper {
		position: relative;
		width: 120px;
		height: 120px; } }

.video__play,
.video__play_button {
	@include vcenter;
	z-index: 10;
	cursor: pointer; }
.video__play_button {
	margin-left: 18px;
	border: 24px solid transparent;
	border-left: 36px solid #fff; }

