@import url("https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&subset=cyrillic");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #00bdca;
  font-family: "Montserrat", sans-serif;
  line-height: 1.2;
  font-size: 16px;
  font-weight: 600;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  width: 100%;
  max-width: 1172px;
  margin: 0 auto;
}

.logo {
  display: inline-block;
  width: 270px;
}

.logo__content {
  display: flex;
  align-items: center;
}

.logo__image {
  display: flex;
  margin-right: 11px;
}

.logo__text h1 {
  color: #fff;
  font-size: 24px;
  font-weight: 600;
}

.logo__text_second {
  color: #56ecf6;
}

.logo__slogan {
  font-size: 13px;
  font-weight: 500;
  line-height: 1;
}

.menu ul {
  display: flex;
  color: #ffffff;
  font-size: 16px;
}

.menu ul li {
  padding: 14px 18px;
  border-radius: 15px;
}

.menu ul li:hover {
  color: #1d354e;
  background-color: #fff;
}

.button {
  display: inline-block;
  padding: 21px 71px;
  font-size: 18px;
  color: #fff;
  font-weight: 600;
  border-radius: 33px;
  background-color: #1d354e;
}

.box {
  position: absolute;
}

.box img {
  display: block;
  width: 100%;
}

.section__title {
  font-size: 60px;
  font-weight: 600;
}

.section__title_white {
  color: #fff;
}

.header__content {
  position: relative;
  display: block;
  height: 146px;
  padding: 62px 75px 0 100px;
}

.header__content:after {
  content: "";
  display: table;
  clear: both;
}

.header__content .menu_main {
  display: inline-block;
  position: absolute;
  text-align: center;
  font-size: 0;
}

.header__content .menu_main:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.header__content .logo {
  margin-right: 125px;
}

.nav_right {
  display: block;
  float: right;
}

.nav_right:after {
  content: "";
  display: table;
  clear: both;
}

.nav_right .account {
  display: inline-block;
  margin-right: 8px;
  padding: 12px 28px;
  color: #1d354e;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  background-color: #fff;
  border-radius: 17px;
}

.language {
  display: flex;
  flex-direction: column;
  float: right;
  padding: 13px 24px;
  color: #ffffff;
  border-radius: 20px;
}

.language_hidden {
  display: none;
}

.language:hover {
  color: #1d354e;
  background-color: #fff;
}

.language:hover .language_hidden {
  display: block;
}

.language a {
  margin-bottom: 17px;
}

.language a:last-child {
  margin-bottom: 0;
}

.language a:hover {
  text-decoration: underline;
}

.forecasts {
  position: relative;
}

.forecasts__content {
  display: flex;
  flex-direction: column;
  padding-top: 84px;
  align-items: flex-end;
}

.forecasts__text {
  margin: 0 90px 210px 0;
  font-weight: 600;
  color: #fff;
  text-align: center;
}

.forecasts__text h3 {
  font-size: 72px;
  margin-bottom: 10px;
  font-weight: 600;
}

.forecasts__subtitle {
  font-size: 30px;
  margin-bottom: 25px;
  line-height: 1.5;
}

.forecasts .box_turquoise {
  top: 248px;
  left: 132px;
}

.forecasts .box_blurred {
  top: 390px;
  right: 190px;
}

.forecasts_bg {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1166px;
  background: url(../img/forecast/forecasts_bg.png) no-repeat;
  background-size: cover;
  z-index: -5;
}

.forecasts__video {
  position: relative;
  width: 100%;
  z-index: -1;
}

.forecasts__video .robot {
  position: absolute;
  top: -759px;
  left: -80px;
}

.forecasts__video .robot img {
  display: block;
  width: 100%;
}

.video {
  position: relative;
  padding: 48px;
  width: 100%;
  height: 720px;
  overflow: hidden;
  background-color: #fff;
  border-radius: 15px;
}

.video video {
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.video__play_wrapper {
  position: relative;
  width: 120px;
  height: 120px;
}

.video__play,
.video__play_button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  cursor: pointer;
}

.video__play_button {
  margin-left: 18px;
  border: 24px solid transparent;
  border-left: 36px solid #fff;
}

.popular {
  color: #fff;
  background-color: #1d354e;
}

.popular__content {
  padding: 160px 100px 0;
}

.popular__title {
  margin-bottom: 15px;
}

.popular__subtitle {
  margin-bottom: 45px;
  font-size: 24px;
  font-weight: 600;
}

.popular__nav {
  margin-bottom: 90px;
}

.popular__nav ul {
  display: flex;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: bold;
}

.popular__nav ul li {
  padding: 20px 30px;
  border: 8px solid #223e5c;
  border-radius: 6px;
  margin-right: 30px;
}

.popular__nav ul li:last-child {
  margin-right: 0;
}

.popular__nav ul li:hover {
  border-color: #00bdca;
  background-color: #00bdca;
}

.popular__data {
  display: flex;
  font-size: 24px;
  font-weight: 700;
}

.popular__data_text {
  position: relative;
  margin-right: 50px;
}

.popular__data_text:last-child {
  margin-right: 0;
}

.popular__data_2:before, .popular__data_3:before {
  position: absolute;
  font-size: 12px;
  font-weight: 500;
  right: 0;
  top: -17px;
}

.popular__data_2:before {
  content: "now";
}

.popular__data_3:before {
  content: "24 hours";
}
