.popular {
    color: #fff;
    background-color: #1d354e;
    &__content {
        padding: 160px 100px 0; }
    &__title {
        margin-bottom: 15px; }
    &__subtitle {
        margin-bottom: 45px;
        font-size: 24px;
        font-weight: 600; }
    &__nav {
        margin-bottom: 90px;
        ul {
            display: flex;
            font-size: 30px;
            text-transform: uppercase;
            font-weight: bold;
            li {
                padding: 20px 30px;
                border: 8px solid #223e5c;
                border-radius: 6px;
                margin-right: 30px;
                &:last-child {
                    margin-right: 0; }
                &:hover {
                    border-color: #00bdca;
                    background-color: #00bdca; } } } }
    &__data {
        display: flex;
        font-size: 24px;
        font-weight: 700;
        &_text {
            position: relative;
            margin-right: 50px;
            &:last-child {
                margin-right: 0; } }
        &_2,
        &_3 {
            &:before {
                position: absolute;
                font-size: 12px;
                font-weight: 500;
                right: 0;
                top: -17px; } }
        &_2 {
            &:before {
                content: "now"; } }
        &_3 {
            &:before {
                content: "24 hours"; } } } }
